import {
  AggregateDataItemsRequest,
  AggregateDataItemsResponse,
  BulkInsertDataItemReferencesRequest,
  BulkInsertDataItemReferencesResponse,
  BulkInsertDataItemsRequest,
  BulkInsertDataItemsResponse,
  BulkPatchDataItemsRequest,
  BulkPatchDataItemsResponse,
  BulkRemoveDataItemReferencesRequest,
  BulkRemoveDataItemReferencesResponse,
  BulkRemoveDataItemsRequest,
  BulkRemoveDataItemsResponse,
  BulkSaveDataItemsRequest,
  BulkSaveDataItemsResponse,
  BulkUpdateDataItemsRequest,
  BulkUpdateDataItemsResponse,
  CountDataItemsRequest,
  CountDataItemsResponse,
  GetDataItemRequest,
  GetDataItemResponse,
  InsertDataItemRequest,
  InsertDataItemResponse,
  IsReferencedDataItemRequest,
  IsReferencedDataItemResponse,
  PatchDataItemRequest,
  PatchDataItemResponse,
  QueryDataItemsRequest,
  QueryDataItemsResponse,
  QueryDistinctValuesRequest,
  QueryDistinctValuesResponse,
  QueryReferencedDataItemsRequest,
  QueryReferencedDataItemsResponse,
  RemoveDataItemRequest,
  RemoveDataItemResponse,
  ReplaceDataItemReferencesRequest,
  ReplaceDataItemReferencesResponse,
  SaveDataItemRequest,
  SaveDataItemResponse,
  SearchDataItemsRequest,
  SearchDataItemsResponse,
  TruncateDataItemsRequest,
  TruncateDataItemsResponse,
  UpdateDataItemRequest,
  UpdateDataItemResponse,
} from './data-item-types'

export interface HttpApiClient {
  getDataItem(r: GetDataItemRequest): Promise<HttpResponse<GetDataItemResponse>>

  insertDataItem(
    r: InsertDataItemRequest
  ): Promise<HttpResponse<InsertDataItemResponse>>

  saveDataItem(
    r: SaveDataItemRequest
  ): Promise<HttpResponse<SaveDataItemResponse>>

  updateDataItem(
    r: UpdateDataItemRequest
  ): Promise<HttpResponse<UpdateDataItemResponse>>

  patchDataItem(
    r: PatchDataItemRequest
  ): Promise<HttpResponse<PatchDataItemResponse>>

  removeDataItem(
    r: RemoveDataItemRequest
  ): Promise<HttpResponse<RemoveDataItemResponse>>

  countDataItems(
    r: CountDataItemsRequest
  ): Promise<HttpResponse<CountDataItemsResponse>>

  queryDistinctValues(
    r: QueryDistinctValuesRequest
  ): Promise<HttpResponse<QueryDistinctValuesResponse>>

  queryDataItems(
    r: QueryDataItemsRequest
  ): Promise<HttpResponse<QueryDataItemsResponse>>

  aggregateDataItems(
    r: AggregateDataItemsRequest
  ): Promise<HttpResponse<AggregateDataItemsResponse>>

  bulkInsertDataItems(
    r: BulkInsertDataItemsRequest
  ): Promise<HttpResponse<BulkInsertDataItemsResponse>>

  bulkUpdateDataItems(
    r: BulkUpdateDataItemsRequest
  ): Promise<HttpResponse<BulkUpdateDataItemsResponse>>

  bulkPatchDataItems(
    r: BulkPatchDataItemsRequest
  ): Promise<HttpResponse<BulkPatchDataItemsResponse>>

  bulkSaveDataItems(
    r: BulkSaveDataItemsRequest
  ): Promise<HttpResponse<BulkSaveDataItemsResponse>>

  bulkRemoveDataItems(
    r: BulkRemoveDataItemsRequest
  ): Promise<HttpResponse<BulkRemoveDataItemsResponse>>

  queryReferencedDataItems(
    r: QueryReferencedDataItemsRequest
  ): Promise<HttpResponse<QueryReferencedDataItemsResponse>>

  bulkInsertDataItemReferences(
    r: BulkInsertDataItemReferencesRequest
  ): Promise<HttpResponse<BulkInsertDataItemReferencesResponse>>

  replaceDataItemReferences(
    r: ReplaceDataItemReferencesRequest
  ): Promise<HttpResponse<ReplaceDataItemReferencesResponse>>

  bulkRemoveDataItemReferences(
    r: BulkRemoveDataItemReferencesRequest
  ): Promise<HttpResponse<BulkRemoveDataItemReferencesResponse>>

  isReferencedDataItem(
    r: IsReferencedDataItemRequest
  ): Promise<HttpResponse<IsReferencedDataItemResponse>>

  truncateDataItems(
    r: TruncateDataItemsRequest
  ): Promise<HttpResponse<TruncateDataItemsResponse>>

  searchDataItems(
    r: SearchDataItemsRequest
  ): Promise<HttpResponse<SearchDataItemsResponse>>
}

export interface HttpResponse<T> {
  data: T
}

export interface Request {
  data?: any
  url?: string
  methodFqn?: string
  method?: string
}

export type RequestInterceptor<R extends Request> = (req: R) => R

export type HttpApiClientFactory<R extends Request> = (
  requestInterceptor: RequestInterceptor<R>
) => HttpApiClient
